import React, { Fragment, useState } from "react";
import { useHistory } from "react-router-dom";
import Modal from "react-modal";
import FormPrograma from "./modals/FormPrograma";
import ProgramService from "../services/program-service";

const customStyles = {
  content: {
    width: "75%",
    height: "100%",
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    backgroundColor: "rgb(58, 58, 58)",
  },
};

Modal.setAppElement("#main");

const HorarioController = (props) => {
  //let subtitle;
  const history = useHistory();
  const programService = new ProgramService();

  const [value, setValue] = useState({
    ann: {},
    form: {
      name: "",
      color: "#563d7c",
      announcer: [],
      moderator: "",
      day: 1,
      hourIni: "00",
      hourFin: "00",
    },
  });

  const handleChange = (e) => {
    setValue({
      form: {
        ...value.form,
        [e.target.name]: e.target.value,
      },
    });
  };

  const handleSelectAnnouncer = (e) => {
    setValue((value) => {
      const announcer = [...value.form.announcer, e];

      return {
        form: {
          ...value.form,
          announcer,
        },
      };
    });
  };

  const handlePopAnnouncer = (e) => {
    setValue((value) => {
        const index = value.form.announcer.indexOf(e)

      const announcer = [
        ...value.form.announcer.slice(0, index),
        ...value.form.announcer.slice(index + 1),
      ];

      return {
        form: {
          ...value.form,
          announcer,
        },
      };
    });
  };

  const handleSubmit = async (e) => {
    console.log(value.form);
    const confirm = await programService.addProgram(value.form);
    if (confirm.err) {
      console.log("Error al obtener la data");
      return;
    }
    console.log(confirm);
    closeModal();
    history.push("/");
    e.preventDefault();
  };

  //handleChange = handleChange.bind(this)
  //handleSubmit = handleSubmit.bind(this)

  const [modalIsOpen, setIsOpen] = useState(false);

  const openModal = () => {
    setIsOpen(true);
  };

  const afterOpenModal = () => {
    //subtitle.style.color = '#f00';
  };

  const closeModal = () => {
    setIsOpen(false);
  };
  return (
    <Fragment>
      <button onClick={openModal} type="button" className="btn btn-dark">
        Añadir Programa
      </button>

      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <FormPrograma
          form={value.form}
          closeModal={closeModal}
          handleChange={handleChange}
          handleSelectAnnouncer={handleSelectAnnouncer}
          handleSubmit={handleSubmit}
          handlePopAnnouncer={handlePopAnnouncer}
        ></FormPrograma>
      </Modal>
    </Fragment>
  );
};

export default HorarioController;
